<script>
    import AppBarPage from '@/components/AppBar.vue'

    import ScanBarComponents from '@/packages/ScanBar.vue'
    import {httpHandle} from '@/http/http'  // api
    import { showSuccessToast,showFailToast,showToast,showDialog } from 'vant';
    import TableComponents from '@/packages/Table.vue'



  export default {
    components:{
        AppBarPage,
        ScanBarComponents,
        TableComponents
    },
    data: () => ({
        bufferRow: {},
        deliverySlipId:'',

        list1:[],  // 物料明细
        list2:[],  // 异常明细


    }),
    created(){
        this.initFunc()
    },
    methods: {
        // 全局 气泡 提示
        GlobalTooltipFunc(text=""){
            showToast({
                message:text,
                overlay:true,
                closeOnClickOverlay:true,
                duration:0
            })
        },
        // 数据字典 格式化
        FormatDictionary(key="",valueKey=""){
            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")
            const _obj=(_bufferDictionaries[key] || [] )
            const _option=_obj.filter(o=>valueKey==o.value)[0]||{}
            return _option
        },
        // 初始化
        async initFunc(){
            const {deliverySlipId=''}=this.$route.query

            this.deliverySlipId=deliverySlipId
            const {code,data={}}= await httpHandle({
                url:`/iiot/deliverySlip/findReceivingData/${deliverySlipId}`,
                method:'get',
            })

            if(code==200){
                this.bufferRow=data.deliverySlip   // 基础数据

                this.list1=data.receivingData  // 物料明细
                // this.list2=data.abnormalList  // 异常明细

                this.$nextTick(()=>{
                    this.$refs.table1.initFunc()
                    this.$refs.table2.initFunc()
                })
            }
        },
        // 头部 查询 111
        async barSearchClick(value=''){
            const _value=value.trim()

            const {code,data=[]}= await httpHandle({
                url:'/iiot/qmTask/listQmTaskSnForSelect',
                method: "get",
                url_params:{
                    sn: _value
                }
            })

            if(code==200){

                if(!data.length){
                    showFailToast("SN号不存在！")
                    return
                }    

                // this.ttPpOrderSnId=data[0]?.ttPpOrderSnId
                showSuccessToast("扫描成功！")


            }
        },
        // 扫码  参评序列号
        barSearchClick2(value=''){

        },
        // 绑定
        async submit(){
            
        },
        // 添加异常
        addErrorHandle(){
            const {bufferRow}=this
            this.$router.push({
                path:'/storage/add', 
                query:{ 
                    deliverySlipId:bufferRow.deliverySlipId,
                    deliverySlipType: bufferRow.deliverySlipType,
                    poNo: bufferRow.poNo,
                    srmNo: bufferRow.srmNo,
                }
            }) 
        },
        // 签收
        async signFunc(){
            const {bufferRow}=this

            showDialog({
                title: '签收',
                message: '确认签收！',
                theme: 'round-button',
                closeOnClickOverlay:true,
            }).then(async () => {

                const {code,data={}}= await httpHandle({
                    url:`/iiot/deliverySlip/receivingData/${bufferRow.deliverySlipId}`,
                    method:'get'
                })


                if(code==200){
                    showSuccessToast('签收成功！')

                    this.$router.go(-1)
                }
            });

        }


    },
  }
</script>
